@font-face {
  font-family: "Circe";
  src: url("../fonts/circe_300.woff2") format("woff2"), url("../fonts/circe_300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Circe";
  src: url("../fonts/circe_600.woff2") format("woff2"), url("../fonts/circe_600.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Circe";
  src: url("../fonts/circe_700.woff2") format("woff2"), url("../fonts/circe_700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
body {
  font-family: "Circle", "Arial", sans-serif;
  font-weight: 300;
  width: 100%;
  max-width: 100%;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.main {
  min-width: 320px;
}
@media (min-width: 768px) {
  .main {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    min-width: 768px;
  }
}
.images {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 290px;
  margin: 0 auto;
}
.images__wrapper {
  display: none;
}
.images__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
}
.images__picture {
  width: 242px;
  height: 351px;
}
@media (min-width: 768px) {
  .images {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-start;
    min-width: calc(768px - 320px - 42px);
    margin-top: 25px;
    margin-left: auto;
    margin-right: 0;
  }
  .images__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-right: 9px;
  }
  .images__list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .images__list li {
    margin-bottom: 15px;
  }
  .images__button {
    width: 42px;
    height: 31px;
    padding: 0;
    box-shadow: inset 0 0 0 1px rgba(222, 222, 222, 0.6);
    border: none;
    background-color: transparent;
  }
  .images__picture {
    width: 290px;
    height: 420px;
  }
}
@media (min-width: 1000px) {
  .images {
    min-width: calc(1000px - 455px - 60px);
    margin-top: 22px;
  }
  .images__wrapper {
    margin-right: 30px;
  }
  .images__list li {
    margin-bottom: 25px;
  }
  .images__preview {
    width: 90px;
    height: 130px;
  }
  .images__picture {
    width: 340px;
    height: 492px;
  }
}
@media (min-width: 1410px) {
  .images {
    width: 650px;
    max-width: 650px;
  }
  .images__wrapper {
    margin-right: 110px;
  }
}
.title {
  display: none;
}
@media (min-width: 768px) {
  .title {
    display: block;
    font-size: 22px;
    line-height: 24px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 0.04em;
    color: #131313;
    order: 2;
  }
}
@media (min-width: 1000px) {
  .title {
    margin-top: 55px;
    margin-bottom: 8px;
  }
}
@media (min-width: 1410px) {
  .title {
    margin-bottom: 5px;
  }
}
.breadcrumbs {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  min-width: 290px;
  padding-right: 15px;
  margin-bottom: 13px;
}
.breadcrumbs li:not(:last-child) {
  margin-right: 10px;
  padding-right: 19px;
  background-image: url("../img/icon_arrow.svg");
  background-repeat: no-repeat;
  background-position: top 50% right 0;
  background-size: 10px 11px;
}
.breadcrumbs li:last-child {
  margin-right: 5px;
}
.breadcrumbs__link {
  font-size: 13px;
  line-height: 20px;
  letter-spacing: 0.01em;
  color: #a2a3a3;
}
@media (min-width: 768px) {
  .breadcrumbs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    order: 1;
    margin-bottom: 2px;
  }
  .breadcrumbs li:not(:last-child) {
    margin-right: 10px;
    padding-right: 22px;
  }
  .breadcrumbs__link {
    font-size: 13px;
    line-height: 20px;
    letter-spacing: 0;
  }
}
@media (min-width: 1000px) {
  .breadcrumbs {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
@media (min-width: 768px) {
  .item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    min-width: 320px;
    margin-top: 20px;
    margin-left: 42px;
    margin-right: auto;
  }
}
@media (min-width: 1000px) {
  .item {
    min-width: 455px;
    margin-left: 60px;
  }
}
@media (min-width: 1410px) {
  .item {
    width: 650px;
    max-width: 650px;
    margin-left: 110px;
  }
}
.keywords {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
